
@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");

* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */

html, body, #root, .App {
  width: 100%;
  height: 100%;
  padding: 0;
  overflow: hidden;
}

body {
  @apply bg-neutral-200;
}

body,
.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.App {
  overflow: auto;
}

.comment-grid {
  @apply max-w-max my-16 p-16 mx-auto border-solid border-black border-2 rounded-2xl;
}

.comment-grid > div {
  @apply my-4 max-w-max mx-auto;
}

.comment-item {
  @apply m-4 py-4 px-2 border-solid border-black border-2 rounded-lg;
}

.comment-item > p, button {
  @apply inline max-w-max mx-4;
}



